import React, {useState} from 'react'
import { PanoViewer, PROJECTION_TYPE } from '@egjs/react-view360'


import vid from '../source/vid2.mp4'
import img from '../source/img.jpg'
import './PVie.css'


function PanoVR() {


	const [startVid, setStartVid] = useState(null)
	const [vvid, setVvid] = useState(vid)
	const start = (e) => {
		setStartVid(e.currentTarget.getVideo())
	}

	const changeFile = (file) => {
		const source = URL.createObjectURL(file);
		setVvid(source)
	}

	const win = <PanoViewer
		tag="div"
		id='viewer'
		video={vvid}
		projectionType={PROJECTION_TYPE.EQUIRECTANGULAR}
		useZoom={false}
		onViewChange={e => e.currentTarget.getVideo()}
		onReady={e => start(e)}
		showPolePoint={true}
	>
	</PanoViewer>

	console.log(win);
	return (<div>
		{win}
		<input title='load' type='file' onChange={e => changeFile(e.target.files[0])} />
		<button onClick={() => startVid.play()}>start video</button>
		<button onClick={() => startVid.pause()}>stop</button>
	</div>
		 
		
	)
}

export default PanoVR